// 添加asu
// src: /api/demo/service_evaluation/create
// data: form asu
Mock.mock('/api/demo/service_evaluation/create', 'post', ({ url, type, body }) => {
  let bodyObj = JSON.parse(body)
  let asu = ServiceEvaluationDB
  if (asu) {
    return {
      code: 0,
      msg: '插入asu成功',
      data: [
        ...asu
      ]
    }
  } else {
    return {
      code: 401,
      msg: '插入asu错误',
      data: {}
    }
  }
});

// 查询service_evaluation
// src: /api/demo/service_evaluation/retrieve
// data: form service_evaluation
Mock.mock('/api/demo/service_evaluation/retrieve', 'post', ({ url, type, body }) => {
  let bodyObj = JSON.parse(body);
  const se = service_evaluationDB;
  se.filter(e => e.evaluation_status = bodyObj.evaluation_status);
  if (se) {
    return {
      code: 0,
      msg: '查询ServiceEvaluation成功',
      data: [
        ...se.reverse()
      ]
    }
  } else {
    return {
      code: 401,
      msg: '查询ServiceEvaluation错误',
      data: {}
    }
  }
});

// 查询单一service_evaluation
// src: /api/demo/service_evaluation?service_id=***
// data: form service_evaluation
Mock.mock(/\/api\/demo\/service_evaluation/, 'get', ({url, type, body}) => {
  //通过正则表达式提取到url后面的id
  let service_evaluation_id = /\/api\/demo\/service_evaluation\/(\S+)/.exec(url);
  let se = service_evaluation_detailDB.filter(e => e.evaluation_id == service_evaluation_id[1]);
  if (se) {
    return {
      code: 0,
      msg: '查询ServiceEvaluation成功',
      data: [
        ...se
      ]
    }
  } else {
    return {
      code: 401,
      msg: '查询ServiceEvaluation错误',
      data: {}
    }
  }
});

Mock.mock('/api/demo/service_evaluation/update', 'post', ({ url, type, body }) => {
  let bodyObj = JSON.parse(body);
  ServiceEvaluationDB[ServiceEvaluationDB.findIndex(e => e.apply_id === bodyObj.apply_id)].apply_status = '3'
  let asu = ServiceEvaluationDB;
  for (let apply in asu) {
    // Object.setPrototypeOf(applyDB[apply],serviceDB.find(e=>e.service_id === applyDB[apply].service_id))
    asu[apply] = extend(asu[apply], serviceDB.find(e => e.service_id === asu[apply].service_id))
    asu[apply] = extend(asu[apply], companyDB.find(e => e.company_id === asu[apply].company_id))
    asu[apply] = extend(asu[apply], userDB.find(e => e.uuid === asu[apply].user_id))
  }
  // let asu = applyDB // .filter(e => e.flag === bodyObj.flag && e.user_id === bodyObj.user_id) // .find()

  if (asu) {
    return {
      code: 0,
      msg: '更新asu成功',
      data: [
        ...asu
      ]
    }
  } else {
    return {
      code: 401,
      msg: '更新asu错误',
      data: {}
    }
  }
})

// 合并对象
function extend (target, source) {
  for (var obj in source) {
    target[obj] = source[obj]
  } return target
} // 测试
