// 查询单一service_info
// src: /api/demo/service_info?service_id=***
// data: form service_info
Mock.mock(/\/api\/demo\/service_info/, 'get', ({url, type, body}) => {
  // console.log('options: ' + url);
  //通过正则表达式提取到url后面的id
  let service_id = /\/api\/demo\/service_info\/(\S+)/.exec(url);
  let si = service_infoDB.find(e => e.service_id == service_id[1]);
  if (si) {
    return {
      code: 0,
      msg: '查询ServiceInfo成功',
      data: si
    }
  } else {
    return {
      code: 401,
      msg: '查询ServiceInfo错误',
      data: {}
    }
  }
});

// 合并对象
function extend (target, source) {
  for (var obj in source) {
    target[obj] = source[obj]
  } return target
} // 测试
